function exportTable(data, fileName) {
	const ws = XLSXutils.utils.json_to_sheet(data)

	const wb = XLSXutils.utils.book_new()
	XLSXutils.utils.book_append_sheet(wb, ws, 'Sheet 1')

	XLSXutils.writeFile(wb, `${fileName}.xlsx`)
}

