import React from 'react';
import classes from './CustomNavbar.module.css';
import { NavLink } from 'react-router-dom';

export const CustomNavbar = ({ user }) => {
	return (
		<div className={classes.navbarContainer}>
			<ul className={classes.mainNavbar}>
				{user.job !== ' ' && (
					<li>
						<NavLink to='/home/parent-panel'>  </NavLink>
						<ul className={classes.nestedBar}>
							{user.job === '' ? (
								<li>
									<NavLink to='/home/add-feedback'>  </NavLink>
								</li>
							) : (
								<li>
									<NavLink to='/home/view-feedback'>  </NavLink>
								</li>
							)}
						</ul>
					</li>
				)}
				{user.job !== '' && (
					<li>
						
						<ul className={classes.nestedBar}>
							{user.job === ' ' && (
								<li>
									<NavLink to='/home/add-task'>   </NavLink>
								</li>
							)}

							<li>
								<NavLink to='/home/view-task'>   </NavLink>
							</li>
						</ul>
					</li>
				)}
				<li>
					
					<ul className={classes.nestedBar}>
						{user.job === ' ' && (
							<li>
								<NavLink to='/home/add-content'>   </NavLink>
							</li>
						)}
						<li>
							<NavLink to='/home/search-content'>   </NavLink>
						</li>
					</ul>
				</li>
				{user.job !== '' && (
					<li>
						<NavLink to='/home/work-plan'>  </NavLink>
					</li>
				)}
			</ul>
		</div>
	);
};
